<template>
  <div>
    <div v-html="temp.resultStyle"></div>
    <div class="start" v-if="!GameStatic.start">
      <div class="card content">
        <h5 class="card-header">歡迎挑戰 Flexbox 海盜</h5>
        <div class="card-body">
          <h2 class="h2 text-center text-success" v-if="data.length">
            目前已有 {{ data[0].total }} 位挑戰者成功過關
          </h2>
          <hr>
          <h5 class="card-title">故事背景</h5>
          <p class="card-text">
            有一天 FLEX 海盜集團襲擊了國王的領土，還偷走了皇冠。
            <br>國王決定派出六角戰艦，欲將 FLEX 海盜集團殲滅並取出皇冠。
            <br>FLEX 海盜集團的活動範圍被分為 20 層，想要拿到皇冠需要達到 FLEX 最深層才行。
            <br>請將<b>座標圖上的準心瞄準到對應的海盜船上</b>，瞄準完成後按下紅色按鈕發射飛彈，一層一層將FLEX海盜集團攻破!
          </p>
          <h5 class="card-title mt-4">遊戲辦法</h5>
          <p class="card-text">
            請將<b>座標圖上的準心瞄準到對應的海盜船上</b>，瞄準完成後按下紅色按鈕發射飛彈，一層一層將FLEX海盜集團攻破!
          </p>
        </div>
        <div class="card-footer text-muted text-right">
          <a href="#" class="btn btn-success" @click.prevent="startGame">馬上開始</a>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-4 col-lg-3 left-con">
          <h2 class="mt-5 h2">Flexbox 挑戰</h2>
          <hr>
          <div class="question mb-3 card">
            <div class="card-body">
            <p class="mb-1">題目  <b>{{levelVal+1}}</b> /20</p>
            {{ game[levelVal].topic }}
            <p class="notice" v-html="game[levelVal].notice"></p>
            </div>
          </div>
          <div class="codes" v-for="(item, index) in game[levelVal].prompt" :key="index">
            <b>{{ item }}</b> {
            <textarea :id="'code'+index" rows="6" @keydown.tab.prevent
            v-model="temp.writeStyle[index]"></textarea>
            <p class="hit" v-if="GameStatic.nextBtn !== '發射'">
              <span>擊中目標</span>
            </p>
            }
          </div>
          <div class="wrong mt-3 card border-danger" v-if="temp.wrongCode">
            <div class="card-body">
              {{ temp.wrongCode }}
            </div>
          </div>
          <button class="btn btn-danger mt-5"
          @click="checkAnswer" v-if="GameStatic.nextBtn == '發射'">
            {{ GameStatic.nextBtn }}
          </button>
          <button class="btn btn-warning mt-5"
          @click="nextPage" v-if="GameStatic.nextBtn !== '發射'">
            下一關
          </button>
        </div>
        <div class="col-8 col-lg-9 playground">
          <nav class="playNav">
            <p class="level">Level.{{ levelVal+1 }}</p>
          </nav>
          <div class="flexbox" :id="'p'+levelVal">
            <h3 class="main" v-if="temp.direction === -1">主軸線</h3>
            <h3 class="vice" v-if="temp.direction === -1">交錯軸</h3>
            <h3 class="vice column" v-if="temp.direction !== -1">主軸線</h3>
            <h3 class="main column" v-if="temp.direction !== -1">交錯軸</h3>
            <div v-html="game[levelVal].html"></div>
            <!-- grids -->
            <div class="flex-container">
              <div class="grid-con">
                <div :class="{main:true, column:temp.direction !== -1}"></div>
                <div :class="{vice:true, column:temp.direction !== -1}"></div>
                <div class="grid" v-for="item in 36" :key="item"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import question from '../../assets/js/question';

export default {
  name: 'Simple',
  data() {
    return {
      data: [],
      levelVal: 0,
      temp: {
        writeStyle: [],
        resultStyle: '',
        wrongCode: '',
        ansRight: true,
        direction: -1,
      },
      GameStatic: {
        start: false,
        nextBtn: '發射',
        end: false,
        second: 0,
      },
      game: question,
    };
  },
  watch: {
    'temp.writeStyle': {
      handler: function cssStyle(val) {
        const vm = this;
        const str = vm.resultCSS(val);
        vm.temp.resultStyle = `<style>${str}</style>`;
        vm.temp.direction = str.replace(/\s+/g, '').indexOf('flex-direction:column');
      },
      deep: true,
    },
  },
  methods: {
    getData() {
      const api = 'https://flexbox-api-test.herokuapp.com/participantcount';
      const vm = this;
      this.$http.get(api).then((response) => {
        vm.data = response.data;
        // console.log(response);
      });
    },
    trimSemiColon(text) {
      return text.slice(-1) === ';' ? text.slice(0, this.length - 1) : text;
    },
    JsonCSS(text) {
      const vm = this;
      if (typeof text !== 'string') {
        console.error('Need a CSS string but given ', typeof text, text);
        return 'Not a valid CSS..!';
      }
      const output = {};
      let lastKey;
      let term;
      let style;
      const regExp = /\r\n|\r|\n|\u2028|\u2029/g;
      try {
        text.split('{').forEach((item) => {
          term = item.trim();
          if (term) {
            if (term.indexOf('}') === -1) {
              // it's a selector
              output[term] = {};
              lastKey = term;
            } else {
              // contains styles and next selector
              term.substring(0, term.indexOf('}')).split(';').forEach((keyValue) => {
                style = keyValue.split(':');
                if (style && style.length === 2) {
                  output[lastKey][style[0].trim().replace(regExp, '')] = vm.trimSemiColon(style[1].trim().replace(regExp, ''));
                }
              });
              try {
                // may be End of Styles
                lastKey = term.split('}')[1].trim();
                if (lastKey) {
                  // for new selector
                  output[lastKey] = {};
                }
              } catch (e) {
                // no more selectors for our life
              }
            }
          }
        });
      } catch (e) {
        return 'Not a valid CSS..!';
      }
      return output;
    },
    resultCSS(val) {
      const vm = this;
      let str = '';
      for (let i = 0; i < val.length; i += 1) {
        str += `
          ${vm.game[vm.levelVal].prompt[i]}{
            ${val[i]}
          }
        `;
      }
      return str;
    },
    checkAnswer() {
      const vm = this;
      // const ans = vm.game[vm.levelVal].ans;
      const { ans } = vm.game[vm.levelVal];
      const objKeyArry = Object.keys(ans);
      let pass = false;
      // 填寫答案物件 {.aims...}
      const UserAnsObj = vm.JsonCSS(vm.resultCSS(vm.temp.writeStyle));
      for (let i = 0; i < objKeyArry.length; i += 1) {
        // .aims
        const objclass = objKeyArry[i];
        // {color:red}
        const userObj = UserAnsObj[objclass];
        const userAnsCssArry = Object.keys(userObj);
        const checkKey = vm.game[vm.levelVal].ans[objclass];
        for (let ii = 0; ii < userAnsCssArry.length; ii += 1) {
          // color
          const userKey = userAnsCssArry[ii];
          // red
          const userVal = userObj[userKey];
          if (ans[objclass][userKey]) {
            if (ans[objclass][userKey] === userVal) {
              // 對
            } else {
              vm.temp.wrongCode = `${userKey} 的值錯誤`;
              vm.temp.ansRight = false;
            }
          } else {
            vm.temp.wrongCode = `不需要用到 ${userKey}`;
            vm.temp.ansRight = false;
            return false;
          }
        }
        pass = Object.keys(checkKey).sort().toString() === userAnsCssArry.sort().toString();
        // console.log(Object.keys(checkKey),userAnsCssArry,vm.temp.ansRight);
      }
      // 如果都正確


      if (pass && vm.temp.ansRight) {
        vm.GameStatic.nextBtn = '擊中目標';
      } else if (vm.temp.wrongCode && pass) {
        vm.temp.ansRight = true;
        vm.temp.wrongCode = '沒有打中';
      }
      return false;
    },
    nextPage() {
      const vm = this;
      if (vm.levelVal === 19) {
        vm.endGame();
      } else {
        vm.levelVal += 1;
        vm.temp.writeStyle = [];
        vm.temp.wrongCode = '';
        vm.GameStatic.nextBtn = '發射';
      }
    },
    startGame() {
      const vm = this;
      const startTime = new Date();
      vm.GameStatic.start = true;
      localStorage.setItem('startTime', String(startTime));
      localStorage.setItem('endTime', '');
    },
    endGame() {
      const vm = this;
      const endTime = new Date();
      vm.GameStatic.end = true;
      localStorage.setItem('endTime', String(endTime));
      this.$router.push('end');
    },
  },
  created() {
    this.getData();
  },
};
</script>

